import vipBrand from '@/components/yoyi-vip-brand/'
import operationButton from '@/components/yoyi-operation-button/'

export default {
  components: {vipBrand, operationButton},
  data() {
    return {
      checked: false,
    }
  },
  methods: {
    checkboxChange(e) {
      if (e.detail && e.detail.value && e.detail.value.length > 0) {
        this.checked = true
      } else {
        this.checked = false
      }
    },
  }
}
